import Ast from "./ast/scene";
var StateHiveKey = {
    Ast: Ast
};
var StateHive = function (game) {
    var hive = {};
    for (var key in StateHiveKey) {
        hive[key] = game.sceneManager.addScene(key, StateHiveKey[key]);
    }
    return hive;
};
export default StateHive;
